 eslint-disable
<template>
  <div class="hello" ref = "hello">
    <img class="setting" @click="setting" src="../assets/setting.png"/>
    <!-- <span v-for="item in textList" class="text-start" :ref="'text'+index" v-show="item.showFlag">文字+1</span> -->
    <span class="text-start" ref="text" v-show="showText">{{msg}}</span>
    <!-- <span class="text-start" ref="text2" v-show="showText">文字+3</span> -->
    <img @touchstart="touchstarted"  @touchend="touchend" ref="img" src="../assets/img_iapp.png"/>
  </div>
</template>

<script>
export default {
  name: 'main-page',
  props: ['msg'],
  data () {
    return {
      i: 0,
      showText: false,
      textList: [{
        node: 0,
        time1: null,
        time2: null,
        showFlag: false,
      },{
        node: 2,
        time1: null,
        time2: null,
        showFlagL: false,
      },{
        node: 3,
        time1: null,
        time2: null,
        showFlagL: false,
      }],
      timer1: null,
      timer2: null,
    }
  },
  watch: {
    i(v){
      if(v>2){
        this.i=0
      }
    },
    msg(v){
      
    }
  },
  mounted(){
    // this.$bus.$on("msg", (data)=>{
    //   this.msg = data
    // })
  },
  created(){

  },
  methods: {
    touchstarted() {
      // var utterThis = new window.SpeechSynthesisUtterance('');
      // window.speechSynthesis.speak(utterThis);
      window.Android.showToast()
      this.i++
      this.showText = true;
      this.$refs.text.className = "text-start"
      window.clearTimeout(this.timer1)
      window.clearTimeout(this.timer1)
      console.log(this.$refs.img)
      this.$refs.img.style.width = "60%"
      this.timer1 = setTimeout(()=>{
        this.$refs.text.className = "text-end"
      },100)
      this.timer2 = setTimeout(()=>{
        this.showText = false;
        this.$refs.text.className = "text-start"
      },1000)
    },
    touchend(){
      var img = this.$refs.img
      img.style.width = "70%"
    },
    showText1(){
      this.showText = true;
      this.$refs.text.className = "text-start"
      window.clearTimeout(this.time1)
      window.clearTimeout(this.time2)
      console.log(this.$refs.img)
      this.$refs.img.style.width = "60%"
      this.time1 = setTimeout(()=>{
        this.$refs.text.className = "text-end"
      },100)
    },
    text(){
      var hello = this.$refs.hello;
      const span = document.createElement("span");
      span.innerHTML = "文字"
      span.className = "text-start"
      hello.appendChild(span)
      hello.lastChild.parser.parse(hello.lastChild);
      // this.i++
      setTimeout(()=>{

      },)
    },
    setting(){
      this.$bus.$emit("setting", true)
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.setting{
  position: absolute;
  top: 3%;
  left: 5%;
  width: 10% !important;
}
.hello{
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background:  #3B3B3B
}
.hello>img{
  width: 70%;
}
.text-start{
  position: absolute;
  margin-left: 35%;
  margin-top: -50%;
  color: #fff;
  font-size: 1.5em
}
.text-end{
  position: absolute;
  margin-left: 35%;
  margin-top: -50%;
  transform: translate(0,-60%);
  transition-duration: 1000ms;
  color: #fff;
  font-size: 1.5em;
  animation-name:hidetip;
  animation-duration:1000ms;
}

@keyframes hidetip {
      from {
        opacity: 1;
      }
      to {
        opacity: 0;
      }
    }
    @-webkit-keyframes hidetip {
      from {
        opacity: 1;
      }
      to {
        opacity: 0;
      }
    }

</style>



