 eslint-disable
<template>
  <div class="setting" ref = "hello">
    <div class="header">
      <img @click="back" src="../assets/arrow-left.png"/>
      <span @click="back">返回</span>
    </div>
    <div class="content">
      <label>文字内容：</label><input v-model="msg"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: ['msg'],
  data () {
    return {
    }
  },
  watch: {
    msg(v){
      this.$bus.$emit("msg", this.msg)
    }
  },
  mounted(){
    
  },
  created(){

  },
  methods: {
    back(){
      this.$bus.$emit("setting", false)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.setting{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  background:  #fff
}
.header{
  margin-top: 7%;
  width: 100%;
  height: 5%;
  display: flex;
  align-items: center
}
.header>img{
  margin-left: 2%;
  margin-right: 2%;
  width: 8%;
}
.header>span{
  font-size: 2em;
  font-family: SimHei;
}
.content{
  margin-top: 10%;
  display: flex;
  align-items: center;
}
.content>label{
  margin-left: 10%;
  font-size: 1.5em;
  font-family: SimHei;
}
.content>input{
  width: 50%;
  height: 100%;
  font-size: 1.3em;
  border: #000 solid;
  color: #000;
  text-align: center
}

</style>



