<template>
  <div id="app">
    <mainPage v-show="showMain" :msg="msg"></mainPage>
    <configure v-show="!showMain" :msg="msg"></configure>
  </div>
</template>

<script>
import mainPage from './components/main-page.vue';
import configure from './components/configure.vue';
export default {
  name: 'App',
  data(){
    return{
      showMain: true,
      msg: "功德+1"
    }
  },
  mounted(){
    this.$bus.$on("setting", (data)=>{
      this.showMain = !data
      console.log(this.showMain)
    })
    this.$bus.$on("msg", (data)=>{
      this.msg = data
    })
  },
  components:{mainPage,configure}
}
</script>

<style>
html,body{
  height: 100%;
}
*,body,html,div,p,img{border:0;margin:0;padding:0;}
#app {
  width: 100%;
  height: 100%;
  color: #2c3e50;
}
</style>



